const Evento = (props) => {
     const { name, url, description, created, changed } = props.info

     let imagen = '';

     if (urlToImage !== '') {
          imagen = <img src={urlToImage} alt={title}
          className="card-img-top" />
     } else {
          imagen = <p className="text-center my-5"> Imagen no disponible </p>
     }
     return (
          <a href={url} target="_blank" className="list-group-item active text-light mb-1">
               <h3 className="mb-3">{name.text}</h3>
          </a>
     );
}

export default Evento;